export default {
  name: 'RecommendRectItem',
  components: {},
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data() {
    return {}
  },
  computed: {
    href() {
      return `https://www.bilibili.com/av${this.item.model_id}`
    },
    ping() {
      return `${process.env.API_URL_BROWSER}v1/flow/spider_report?type=click&id=${this.item.id}`
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {}
}
