export default {
  name: 'HomepageFlow',
  components: {},
  props: {
    func: {
      required: true,
      type: String
    },
    type: {
      type: String,
      default: 'page'
    },
    auto: {
      type: Number,
      default: 1
    },
    query: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    handleCallback({ data }) {
      const { result } = data
      if (!result.length) {
        return
      }
      this.$emit('callback', result)
    }
  }
}
