export default {
  name: 'PageBanner',
  props: {
    background: {
      required: true,
      type: String
    }
  },
  data() {
    return {
      keyword: ''
    }
  },
  methods: {
    handleSearch() {
      if (!this.keyword) {
        return
      }
      window.open(`https://search.bilibili.com/all?keyword=${encodeURIComponent(this.keyword)}&from_source=calibur`)
    }
  }
}
