import { throttle } from 'throttle-debounce'

export default {
  name: 'VerticalNavBar',
  components: {},
  props: {
    menu: {
      required: true,
      type: Array
    }
  },
  data() {
    return {
      atTop: true,
      offsetTop: 0,
      offsets: [],
      active: -1
    }
  },
  computed: {
    top() {
      return this.atTop ? '232px' : this.offsetTop
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.calcOffset()
      window.addEventListener('scroll', this.onScroll)
    })
  },
  methods: {
    onScroll: throttle(100, function() {
      const scrollTop = Math.max(
        window.pageYOffset,
        document.documentElement.scrollTop,
        document.body.scrollTop
      )

      this.atTop = scrollTop <= 220

      if (scrollTop <= 0) {
        this.active = -1
        return
      }

      const list = this.offsets.map(_ => Math.abs(_ - scrollTop))
      this.active = list.indexOf(Math.min(...list))
    }),
    calcOffset() {
      this.offsets = []
      const list = document.querySelectorAll('.anchor-module')
      const scrollTop = Math.max(
        window.pageYOffset,
        document.documentElement.scrollTop,
        document.body.scrollTop
      )
      ;[].forEach.call(list, (item) => {
        const rect = item.getBoundingClientRect()
        this.offsets.push(scrollTop + rect.top)
      })
      const rectHgt = this.$el.getBoundingClientRect().height
      const screenHgt = document.documentElement.clientHeight
      if (rectHgt >= screenHgt) {
        this.offsetTop = '0px'
      } else {
        this.offsetTop = `${(screenHgt - rectHgt) / 2 | 0}px`
      }
      const offsets = this.offsets.map(_ => Math.abs(_ - scrollTop))
      this.active = offsets.indexOf(Math.min(...offsets))
    }
  }
}
