import HomepageFlow from '~/components-new/HomepageFlow'
import RecommendRectItem from '~/components-new/RecommendRectItem'

export default {
  name: 'RollRecommended',
  components: {
    HomepageFlow,
    RecommendRectItem
  },
  props: {},
  data() {
    return {
      activeIndex: 0
    }
  },
  computed: {
    leftText() {
      const index = this.activeIndex
      if (index === 0) {
        return '昨日'
      } else if (index === 1) {
        return '三日'
      }
      return '一周'
    },
    rightText() {
      const index = this.activeIndex
      if (index === 0) {
        return '一周'
      } else if (index === 1) {
        return '昨日'
      }
      return '三日'
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    handleChangeRecommended(index) {
      this.activeIndex = index
    },
    switchRecommended(isNext) {
      isNext ? this.$refs.switcher.next() : this.$refs.switcher.prev()
    },
    handleLoaded() {}
  }
}
