import BangumiIntroItem from '~/components-new/BangumiIntroItem'

export default {
  name: 'BangumiSwitcher',
  components: {
    BangumiIntroItem
  },
  computed: {
    defaultIndex() {
      const week = new Date().getDay()
      return (week || 7) - 1
    },
    resource() {
      return this.$store.getters['flow/getFlow']({
        func: 'getBangumiRelease',
        type: 'page'
      })
    },
    list() {
      return this.resource ? this.resource.result : [[], [], [], [], [], [], []]
    },
    headers() {
      return [
        {
          text: '一',
          'text-active': '周一'
        },
        {
          text: '二',
          'text-active': '周二'
        },
        {
          text: '三',
          'text-active': '周三'
        },
        {
          text: '四',
          'text-active': '周四'
        },
        {
          text: '五',
          'text-active': '周五'
        },
        {
          text: '六',
          'text-active': '周六'
        },
        {
          text: '日',
          'text-active': '周日'
        }
      ]
    }
  },
  methods: {
    handleLazy() {
      this.$refs.loader.initData()
    },
    handleTabSwitch() {
      if (this.resource && this.resource.error) {
        this.$refs.loader.initData()
      }
    },
    handleRetry() {
      this.$refs.loader.retry()
    }
  }
}
