import { getMenuCounter } from '~/api/homepageApi'

export default {
  name: 'MenuBar',
  filters: {
    convertCount(value) {
      if (!value) {
        return '- -'
      }
      if (value > 999) {
        return '999+'
      }
      return value
    }
  },
  props: {
    menu: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      list: this.menu
    }
  },
  mounted() {
    this.getCounter()
  },
  methods: {
    getCounter() {
      getMenuCounter(this)
        .then((obj) => {
          Object.keys(obj).forEach((key) => {
            this.list.forEach((item) => {
              if (item.id.toString() === key.toString()) {
                item.count = +obj[key]
              }
            })
          })
        })
        .catch(() => {})
    },
    convertPing(item) {
      return `${process.env.API_URL_BROWSER}v1/cm/report_menu_click?id=${item.id}&type=${item.type}`
    }
  }
}
